import React from "react";

import './store_features.scss'
export default function HeroSupport() {
  return (
    <section
      id="interactive3"
      className="interactive interactive-2 interactive-3 store-features"
    >
      <div className="container">
        <div className="row">
          <div className="col-sm-12 col-md-12 col-lg-12">
            <div className="heading heading-3 mb-100 text-center">
              <h2 className="heading--title">DopeGood STORE</h2>
            </div>
          </div>
          {/* .col-lg-12 end  */}
        </div>
        {/* .row end  */}
        <div className="row ">
          <div className="col-sm-12 col-md-12 col-lg-4">
            <div className="banner-panel">
              <div className="num">F</div>
              <div className="panel--content">
                <h5>Free Shipping</h5>
                <h6>ALL ORDER OVER $30</h6>
                <p>
                  We offer integral communication services, and we're
                  responsible for our process.
                </p>
              </div>
            </div>
          </div>
          {/* .interactive-panel end  */}
          <div className="col-sm-12 col-md-12 col-lg-4">
            <div className="banner-panel">
              <div className="num">S</div>
              <div className="panel--content">
                <h5>Support Customer</h5>
                <h6>SUPPORT 24/7</h6>
                <p>
                  The only thing that can match our love for pretty things is
                  our passion for helping our customers.
                </p>
              </div>
            </div>
          </div>
          {/* .interactive-panel end  */}
          <div className="col-sm-12 col-md-12 col-lg-4">
            <div className="banner-panel">
              <div className="num">P</div>
              <div className="panel--content">
                <h5>Payment security</h5>
                <h6>100% SECURE PAYMENT</h6>
                <p>
                  We use the most trusted and secure payment methods to make
                  customers comfortable about their purchase.
                </p>
              </div>
            </div>
          </div>
          {/* .interactive-panel end  */}
        </div>
        {/* .row end  */}
      </div>
      {/* .container end  */}
    </section>
  );
}
